.form__input_theme-dark {
	margin-top: 18px;
	color: #CCCCCC;
	background-color: #000000;
	border-bottom: 2px solid #CCCCCC;
}

.form__input-icon_inv {
  position: absolute;
  right: 2px;
  width: 20px;
	height: 20px;
	background-image: url(../../../../images/eye_closedn.png);
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;
  cursor: pointer;
}

.form__input-icon_vis {
	background-image: url(../../../../images/eye_openedn.png);
}

@media screen and (max-width: 619px) {
	.form__input_theme-dark {
		width: 260px;
	}
}